import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    mybooks: JSON.parse(window.localStorage.getItem("mybooks")) || [],
  },
  mutations: {
    addMybooks: (state, book) => {
      state.mybooks = [...state.mybooks, ...[book]];
      window.localStorage.setItem("mybooks", JSON.stringify(state.mybooks));
    },
    removeMybooks: (state, id) => {
      state.mybooks = state.mybooks.filter((item) => item._id !== id);
      window.localStorage.setItem("mybooks", JSON.stringify(state.mybooks));
    },
  },
  actions: {
  },
  modules: {
  }
})
