<template>
  <div class="detailbookreview">
    <h5>热门书评</h5>
    <ul>
      <li v-for="n in strings" :key="n._id">
        <div class="inner">
          <img :src="`https://statics.zhuishushenqi.com/${n.author.avatar}`" class="avater" />
          <div class="right">
            <p class="name">{{ n.author.nickname }}</p>
            <p class="title">{{ n.title }}</p>
            <p class="score">
              <i class="star-full"></i>
              <i class="star-full"></i>
              <i class="star-full"></i>
              <i class="star-full"></i>
              <i class="star-empty"></i>
            </p>
            <p class="content">
              {{ n.content }}
            </p>
            <p class="love">
              <span> {{ n.updated | timeTier }}</span>
              <span> {{ n.commentCount }}人觉得有用</span>
            </p>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: { strings: Array },
  computed: {},
  filters: {
    timeTier(t) {
      // 思路：用现在时间-去更新时间 拆分减 判断年月日时分，显示
      let x = t.split("T")[0];
      let y = t.split("T")[1].split("Z")[0];
      let z = x.concat(" ", y);
      var da = new Date(z);
      var a = da.getHours();
      a = a === 0 ? a + 1 : a;
      var dates = a + "小时前";
      return [dates].join("");
    },
  },
};
</script>

<style lang="less" scoped>
.detailbookreview {
  h5 {
    padding: 20px 0 10px 20px;
    line-height: 1.5;
    font-size: 14px;
    font-weight: 400;
  }
  ul {
    li {
      font-size: 12px;
      .inner {
        position: relative;
        left: 20px;
        top: 0;
        padding: 10px 5px 5px 45px;
        border-bottom: 1px solid #ebebeb;
        .avater {
          position: absolute;
          top: 10px;
          left: 0;
          width: 35px;
          height: 35px;
          border-radius: 50%;
        }
        .right {
          color: #999;
          width: 80vw;
          .name {
            position: relative;
            height: 15px;
            line-height: 1.2;
            color: #a58d5e;
            font-size: 12px;
          }
          .title {
            height: 20px;
            line-height: 1.6;
            font-weight: 700;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            color: #000;
          }
          .score {
            height: 25px;
            padding: 5px 0;
            font-size: 0;
            .star-empty {
              display: inline-block;
              vertical-align: middle;
              width: 12px;
              height: 12px;
              background: url(data:image/png;base64,)
                50% no-repeat;
              background-size: 80%;
              background-repeat: no-repeat;
              background-position: 0;
            }
            .star-full {
              display: inline-block;
              vertical-align: middle;
              width: 12px;
              height: 12px;
              background: url(data:image/png;base64,)
                50% no-repeat;
              background-size: 80%;
              background-repeat: no-repeat;
              background-position: 0;
            }
          }
          .content {
            height: 38px;
            overflow: hidden;
            line-height: 1.5;
            text-align: justify;
          }
          .love {
            // padding-top: 10px;
            height: 20px;
            overflow: hidden;
            span {
              &:first-child {
                float: left;
              }
              &:last-child {
                float: right;
              }
            }
          }
        }
      }
    }
  }
}
</style>
