<template>
  <div class="detail" v-if="detail">
    <BookTitle>书籍详情</BookTitle>
    <DetailTitle :book="detail" />
    <section class="btn">
      <van-button color="lightgray" v-if="isExit" @click="removeMybooks(detail._id)">撤出书架</van-button>
      <van-button plain type="primary" color="#b93321" v-else @click="addMybooks(detail)">
        加入书架
      </van-button>
      <van-button type="primary" color="#b93321" @click="$router.push(`/reading?id=0&bookid=${detail._id}`)"
        >开始阅读</van-button
      >
    </section>
    <section class="reader">
      <p class="item">
        <span class="key">追人气</span>
        <span> {{ detail.latelyFollower | parseCount }} </span>
      </p>
      <p class="item">
        <span class="key">读者留存率</span>
        <span> {{ detail.retentionRatio }}%</span>
      </p>
      <p class="item">
        <span class="key">日更字数/天</span>
        <span> {{ detail.serializeWordCount | tise }}</span>
      </p>
    </section>
    <section class="list">
      <div class="introduction">
        <p :class="{ active: active }" style="user-select: text;">
          {{ detail.longIntro | longIntroTrem }}
        </p>
        <span class="arrow" :class="{ up: active }" @click="active = !active"></span>
      </div>
      <div class="ad-item"></div>
      <div class="chapter-item" @click="$router.push('/detail/chapter?id=' + detail._id)">
        <div class="inner">
          目录
          <i class="arrow"></i>
          <span>{{ detail.lastChapter }}</span>
        </div>
      </div>
    </section>
    <DetailBookReview :strings="strings" />
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import { getDetail, getDiscusSions } from "@/apis/index.js";

import DetailTitle from "@/components/DetailTitle.vue";
import BookTitle from "@/components/BookTitle.vue";
import DetailBookReview from "@/components/DetailBookReview.vue";

export default {
  components: {
    DetailTitle,
    BookTitle,
    DetailBookReview,
  },
  data: function() {
    return {
      detail: null,
      active: false,
      strings: null,
    };
  },
  filters: {
    longIntroTrem(t) {
      t = t.split("\n");
      let x = "";
      t.forEach((v) => {
        v = v.trim();
        x = x.concat(v);
      });
      return x;
    },
    parseCount: function(value) {
      if (value > 100000000) {
        return (value / 100000000).toFixed(1) + "亿";
      } else if (value > 10000) {
        return (value / 10000).toFixed(1) + "万";
      } else {
        return value;
      }
    },
    tise(a) {
      if (a > 0) {
        return a;
      } else {
        return 0;
      }
    },
  },
  computed: {
    ...mapState(["mybooks"]),
    isExit: function() {
      return this.mybooks.some((item) => item._id === this.detail._id);
    },
  },
  created: function() {
    getDetail(this.$route.query.id).then((res) => (this.detail = res.data));
    getDiscusSions(this.$route.query.id).then((res) => (this.strings = res.data.posts));
  },
  methods: {
    ...mapMutations(["addMybooks", "removeMybooks"]),
  },
};
</script>

<style lang="less" scoped>
.detail {
  .van-nav-bar {
    .title,
    .left {
      color: white;
    }
  }
  .btn {
    display: flex;
    justify-content: space-around;
    margin: 5px 0 15px 0;
    .van-button {
      font-size: 16px;
      width: 145px;
      height: 40px;
    }
  }
  .reader {
    display: flex;
    padding: 10px 0;
    border-top: 1px solid #ebebeb;
    border-bottom: 1px solid #ebebeb;
    font-size: 0;
    background: #fff;
    .item {
      display: flex;
      flex-direction: column;
      width: 33%;
      text-align: center;
    }
    .key {
      color: #999;
      font-size: 12px;
    }
    span {
      &:last-child {
        margin-top: 5px;
        font-size: 16px;
      }
    }
  }
  .list {
    .introduction {
      position: relative;
      padding: 15px 15px 15px 17px;
      p {
        line-height: 1.8;
        font-size: 13px;
        word-break: break-all;
        text-overflow: ellipsis;
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        transition: all 0.4s ease;
        &.active {
          display: block;
        }
      }
      .arrow {
        position: absolute;
        width: 20px;
        height: 20px;
        bottom: 14px;
        right: 5px;
        background: url(data:image/png;base64,)
          50% no-repeat;
        background-size: 60%;
        background-repeat: no-repeat;
        background-position: 50%;
        transition: all 0.1s ease;
      }
      .up {
        transform: rotate(180deg);
      }
    }
    .ad-item {
    }
    .chapter-item {
      display: block;
      border-bottom: 1px solid #ebebeb;
      line-height: 3;
      color: #333;
      .inner {
        position: relative;
        font-size: 14px;
        padding: 5px 3px 5px 17px;
        border-top: 1px solid #f4f4f4;
        span {
          float: right;
          max-width: 60%;
          color: #999;
          word-break: break-all;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
        }
        .arrow {
          float: right;
          width: 20px;
          height: 20px;
          margin-top: 10px;
          background: url(data:image/png;base64,)
            50% no-repeat;
          background-size: 60%;
          background-repeat: no-repeat;
          background-position: 50%;
          transform: rotate(-90deg);
        }
      }
    }
  }
}
</style>
