<template>
  <section class="reading" v-if="read" @scroll="scroll" ref="read">
    <section class="content">
      <div class="cnt" v-for="(t, index) in title" :key="index">
        <div class="title">{{ t }}</div>
        <div class="con" v-html="opcon[index]"></div>
      </div>

      <section class="mask" @click.stop="show = true">
        <div v-show="show" class="titles">
          <p @click="$router.go(-1)">返回</p>
          <p>{{ read.title }}</p>
          <p @click="icon = !icon">icon</p>
        </div>
        <div
          v-show="show"
          class="msk"
          @click.stop="
            show = false;
            icon = false;
          "
        >
          <ul v-if="icon">
            <li @click.stop="$router.push('/')">首页</li>
            <li @click.stop="$router.push('/mybooks')">书架</li>
            <li @click.stop="$router.push('/detail?id=' + bookid)">详情</li>
          </ul>
        </div>

        <div v-show="show" class="bottom">
          <p @click="PreviousChapter">上一个</p>
          <p @click="NextChapter">下一个</p>
          <!-- <van-pagination v-model="currentPage" :page-count="12" mode="simple" /> -->
        </div>
      </section>
    </section>
  </section>
</template>

<script>
import { getChapterstwo } from "@/apis/index.js";
export default {
  components: {},
  data() {
    return {
      read: null,
      show: false,
      icon: false,
      bookid: this.$route.query.bookid,
      opcon: [],
      title: [],
      getconttop: null,
      getcont: null,
      id: 0,
      loading: false,
      finished: false,
      currentPage: 1,
    };
  },
  methods: {
    scroll(event) {
      if (event.target.offsetHeight + event.target.scrollTop === event.target.scrollHeight) {
        console.log("触底");
        this.OpSize();
      }
    },
    showPopup() {
      this.show = true;
    },
    OpSize() {
      this.id += 1;
      this.getconttop(this.id);
    },
    onLoad() {
      this.OpSize();
      this.loading = false;
    },
    PreviousChapter() {
      this.id--;
      this.id = this.id >= 1 ? this.id : 1;
      this.getcont(this.id);
      this.$refs.read.scrollTop = 0;
    },
    NextChapter() {
      this.id++;
      this.getcont(this.id);
      this.$refs.read.scrollTop = 0;
    },
  },

  created() {
    this.id = Number(this.$route.query.id) + 1;
    this.getcont = (id) => {
      getChapterstwo(id).then((res) => {
        this.read = res.data;
        this.title = [res.data.title];
        this.opcon = [res.data.cpContent.replaceAll("<p>", '<p style="text-indent: 2em;">')];
      });
    };
    this.getconttop = (id) => {
      getChapterstwo(id).then((res) => {
        this.read = res.data;
        this.title = [...this.title, res.data.title];
        this.opcon = [...this.opcon, res.data.cpContent.replaceAll("<p>", '<p style="text-indent: 2em;">')];
      });
    };
    this.getcont(this.id);
  },
  mounted() {
    // this.OpSize();
  },
  watch: {},
};
</script>

<style lang="less" scoped>
.reading {
  width: 100vw;
  height: 100vh;
  overflow: scroll;
  .content {
    position: relative;
    padding-top: 25px;
    .cnt {
      margin-bottom: 30px;
      .title {
        padding: 0 20px;
        font-size: 18px;
        font-weight: 600;
      }
      .con {
        padding: 10px 20px 0 20px;
        p {
          text-indent: 2em;
        }
      }
    }
  }
  .mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100%;
    .titles {
      width: 100vw;
      height: 8vh;
      padding: 0 20px;
      font-size: 14px;
      position: fixed;
      top: 0;
      left: 0;
      background-color: rgba(50, 51, 52, 0.9);
      color: white;
      display: flex;
      justify-content: space-between;
      align-items: center;
      p {
      }
    }
    .msk {
      width: 100vw;
      height: 67vh;
      position: fixed;
      top: 8vh;
      left: 0;
      ul {
        width: 90px;
        height: 90px;
        border-radius: 5px;
        position: fixed;
        top: 10vh;
        right: 3vw;
        padding-top: 5px;
        background-color: #323334;
        color: white;
        z-index: 9;
        &::after {
          content: "";
          position: absolute;
          top: -13px;
          right: 5vw;
          width: 0;
          height: 0;
          border: 7px solid transparent;
          border-bottom: 7px solid #323334;
        }
        li {
          font-size: 14px;
          text-align: center;
          line-height: 1.8;
          &:nth-child(2) {
            border-top: 1px solid #222;
            border-bottom: 1px solid #222;
          }
        }
      }
    }
    .bottom {
      width: 100vw;
      height: 25vh;
      position: fixed;
      bottom: 0;
      left: 0;
      color: white;
      background-color: rgba(50, 51, 52, 0.9);
      display: flex;
      justify-content: space-between;
    }
  }
}
</style>
