<template>
  <li class="book-item">
    <van-image width="60" height="80" :src="book.cover | parseImageUrl" />
    <section class="info">
      <h4 class="name">
        <span>{{ book.title }}</span>
      </h4>
      <p class="sku">
        <a href="#">
          <span class="red"> {{ book.author }}</span>
        </a>
        <i>|</i>
        <span>{{ book.minorCate }}</span>
        <i>|</i>
        <span>{{ book.wordCount | parseCount }}</span>
      </p>
      <p class="skz">
        <i class="" v-if="!book.allowMonthly"> {{ book.updated | timeTier }}更新</i>
        <i class="" v-else>完结</i>
      </p>
    </section>
  </li>
</template>

<script>
export default {
  props: ["book"],
  filters: {
    parseImageUrl: function(n) {
      return decodeURIComponent(n.replace(/\/agent\//gi, ""));
    },
    parseCount: function(value) {
      if (value > 100000000) {
        return (value / 100000000).toFixed(1) + "亿";
      } else if (value > 10000) {
        return (value / 10000).toFixed(1) + "万";
      } else {
        return value;
      }
    },
    timeTier(t) {
      // 思路：用现在时间-去更新时间 拆分减 判断年月日时分，显示
      let x = t.split("T")[0];
      let y = t.split("T")[1].split("Z")[0];
      let z = x.concat(" ", y);
      var da = new Date(z);
      var dates = da.getHours() + "小时前";
      return [dates].join("");
    },
  },
};
</script>

<style lang="less" scoped>
.book-item {
  margin-top: 10px;
  display: flex;
  padding: 10px;
  .van-image {
    margin-left: 10px;
  }
  .info {
    margin-left: 14px;
    flex: 1;
    .name {
      color: #333;
      font-weight: 400;
      span {
        font-size: 15px;
        font-weight: 700;
        display: inline-block;
        vertical-align: middle;
        max-width: 70%;
        height: 100%;
        line-height: 1;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
      }
    }
    .sku {
      font-size: 12px;
      color: #999;
      margin-top: 5px;
      a {
        .red {
          color: #b93321;
        }
      }
      i {
        margin: 0 10px;
        font-style: normal;
      }
      span {
        display: inline-block;
        vertical-align: middle;
      }
    }
    .skz {
      margin-top: 5px;
      i {
        color: #999;
        font-size: 10px;
        font-style: normal;
      }
    }
    // 文字简介
    .desc {
      font-size: 12px;
      color: #999;
      margin: 5px 3px 8px 0;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}
</style>
