<template>
  <div class="source">
    <BookTitle>登录</BookTitle>
    <section class="con" v-if="dlzc">
      <van-form @submit="onSubmit" is-link>
        <van-field
          v-model="username"
          label="账号"
          name="validator"
          placeholder="请输入账号"
          :rules="[{ validator, message: '账号必须是5-10位数字或字母组成，开头不能是数字' }]"
        />
        <van-field
          v-model="password"
          type="password"
          label="密码"
          name="passwordor"
          placeholder="请输入密码"
          :rules="[{ validator: passwordor, message: '密码必须包含数字,英文,字符中的两种以上，长度6-20' }]"
        />

        <div style="margin: 16px;">
          <van-button
            style=" border: none; background: #b93321;"
            round
            block
            type="info"
            native-type="submit"
          >
            <span v-if="showm">登录</span>
            <van-loading v-if="!showm" color="#fff">登录中...</van-loading>
          </van-button>
        </div>
      </van-form>

      <van-popup
        v-model="show"
        position="top"
        :style="{
          width: '88vw',
          height: '12vh',
          marginTop: '10vh',
          marginLeft: ' 4vh',
          borderRadius: '10px',
          paddingTop: ' 2vw',
        }"
      >
        <p class="mm" v-if="pscws">密码错误，请重试</p>
        <div v-if="sfzcs" class="zc">
          <p>当前账号还未注册是否快速注册，并登录？</p>
          <div class="btn">
            <van-button
              @click="
                accountCr(dqacc);
                one = true;
                show = false;
              "
            >
              确定
            </van-button>
            <van-button
              @click="
                sfzcs = false;
                show = false;
              "
            >
              取消
            </van-button>
          </div>
        </div>
      </van-popup>
    </section>
    <section v-if="!dlzc">
      <p>登录成功</p>
      <div class="noe" v-if="one">
        <p>请保存好账号密码</p>
        <p>账号：{{ username }}</p>
        <p>密码：{{ password }}</p>
      </div>
    </section>
  </div>
</template>

<script>
import BookTitle from "@/components/BookTitle.vue";
export default {
  components: {
    BookTitle,
  },
  data() {
    return {
      username: "",
      password: "",
      show: false,
      showm: true,
      pscw: false,
      sfzc: false,
      pscws: false,
      sfzcs: false,
      dlzc: true,
      one: false,
      dqacc: [],
      account: JSON.parse(window.localStorage.getItem("account")) || [],
    };
  },
  methods: {
    showPopup() {
      this.show = true;
    },
    onSubmit(values) {
      //   console.log("submit", values);
      if (!this.account[0]) {
        this.account = [...new Set([...this.account, []])];
        window.localStorage.setItem("account", JSON.stringify(this.account));
      }
      this.dqacc = values;
      this.sfzcs = false;
      this.pscws = false;
      this.account.forEach((i) => {
        if (values.validator === i.validator) {
          if (values.passwordor === i.passwordor) {
            // console.log("登录成功");
            this.showm = false;
            this.LogIn();
          } else {
            // console.log("密码错误");
            this.pscw = true;
          }
        } else {
          this.sfzc = true;
        }
      });
      if (this.showm) {
        if (this.sfzc) {
          if (this.pscw) {
            console.log("密码错误");
            this.pscws = true;
            this.showPopup();
          } else {
            this.sfzcs = true;
            this.showPopup();
          }
        }
      } else {
        console.log("登录成功");
      }
      //   初始化
      this.pscw = false;
      this.sfzc = false;
      //   this.accountCr(values);
    },
    accountCr(v) {
      this.account = [...new Set([...this.account, v])];
      window.localStorage.setItem("account", JSON.stringify(this.account));
      this.onSubmit(v);
      this.sfzc = false;
    },
    LogIn() {
      setInterval(() => {
        this.showm = true;
        this.dlzc = false;
      }, 3000);
    },
    validator(val) {
      return /^[a-zA-Z][a-zA-Z0-9]{4,9}$/.test(val);
    },
    passwordor(val) {
      return /^(?![0-9]+$)(?![a-z]+$)(?![A-Z]+$)(?!([^(0-9a-zA-Z)])+$).{6,20}$/.test(val);
    },
  },
};
</script>

<style lang="less" scoped>
.source {
  text-align: center;
  .con {
    // .van-form {
    // }
    .van-popup {
      .mm {
        color: red;
        margin-top: 20px;
      }
      .zc {
        p {
          border-bottom: 1px solid grey;
        }
        .btn {
          margin-top: 1vw;
          display: flex;
          justify-content: space-evenly;
          .van-button {
            border-radius: 8px;
          }
        }
      }
    }
  }
}
</style>
