<template>
  <li class="book-item" @click="$router.push('/detail?id=' + book._id)">
    <van-image width="60" height="80" :src="book.cover | parseImageUrl" />
    <section class="info">
      <h4 class="name">
        <span>{{ book.title | titlecl }}</span>
        <i class="" v-if="!book.allowMonthly"> 连载 </i>
        <i class="complete" v-else> 完结 </i>
      </h4>
      <p class="desc">
        {{ book.shortIntro | longIntroTrem }}
      </p>
      <p class="category">
        {{ book.author }}
        <span class="split">|</span>
        <span class="red"> {{ book.latelyFollower | parseCount }}</span>
        人气
        <i class="tag" v-if="!book.majorCaten">{{ book.majorCate }}</i>
      </p>
    </section>
  </li>
</template>

<script>
export default {
  props: ["book"],
  filters: {
    parseImageUrl: function(n) {
      return decodeURIComponent(n.replace(/\/agent\//gi, ""));
    },
    titlecl(t) {
      if (t.lastIndexOf("（") != -1) {
        let z = t.substring(t.lastIndexOf("（"), t.length);
        t = t.replace(z, "");
        return t;
      } else {
        return t;
      }
    },
    longIntroTrem(t) {
      t = t.split("\n");
      let x = "";
      t.forEach((v) => {
        v = v.trim();
        x = x.concat(v);
      });
      return x;
    },
    parseCount: function(value) {
      if (value > 100000000) {
        return (value / 100000000).toFixed(1) + "亿";
      } else if (value > 10000) {
        return (value / 10000).toFixed(1) + "万";
      } else {
        return value;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.book-item {
  display: flex;
  padding: 10px;
  .info {
    margin-left: 14px;
    flex: 1;
    .name {
      color: #333;
      font-weight: 400;
      span {
        display: inline-block;
        vertical-align: middle;
        max-width: 70%;
        height: 100%;
        line-height: 1;
        white-space: nowrap;
        text-overflow: ellipsis;
        font-size: 14px;
        overflow: hidden;
      }
      i {
        display: inline-block;
        margin-left: 5px;
        border-radius: 2px;
        background-color: #b93321;
        font-style: normal;
        color: #fff;
        font-size: 11px;
        transform: scale(0.8);
        padding: 1px 2px;
        &.complete {
          background-color: green;
        }
      }
    }
    .desc {
      font-size: 12px;
      color: #999;
      margin: 5px 3px 8px 0;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    .category {
      font-size: 13px;
      line-height: 1.5;
      position: relative;
      overflow: hidden;
      color: #666;
      .split {
        padding: 0 5px;
      }
      .red {
        color: #b93321;
      }
      .tag {
        position: absolute;
        right: 0;
        top: 2.5px;
        padding: 0 3px;
        height: 14px;
        line-height: 1;
        border: 1px solid #ff8400;
        color: #ff8400;
        font-style: normal;
        font-size: 12px;
        border-radius: 2px;
        transform: scale(0.8);
      }
    }
  }
}
</style>
