<template>
  <div class="chapter" v-if="summary">
    <BookTitle> {{ summary.name }} </BookTitle>
    <ul>
      <div class="chapter-header">
        <div class="chapter-headers">
          <h3 class="chapter-title">目录</h3>
          <span class="chapter-title-desc" v-if="chapters">共{{ chapters.length }}章</span>
        </div>
        <span class="chapterBtn" @click="RreverseChapters">{{ x ? "正序" : "倒序" }}</span>
      </div>
      <div class="chapter-body">
        <li
          v-for="(t, index) in chapters"
          :key="t._id"
          @click="$router.push(`/reading?id=${index}&bookid=${bookid}`)"
        >
          {{ t.title }}
          <span class="vip" v-if="t.isVip"></span>
        </li>
      </div>
    </ul>
  </div>
</template>

<script>
import BookTitle from "@/components/BookTitle.vue";
import { getSummary, getBookChapters } from "@/apis/index.js";

export default {
  components: {
    BookTitle,
  },
  data() {
    return {
      summary: null,
      chapters: null,
      x: false,
      bookid: this.$route.query.id,
    };
  },
  filters: {
    chaptersurl(v) {
      decodeURIComponent(v);
    },
  },
  methods: {
    RreverseChapters() {
      this.x = !this.x;
      this.chapters.reverse();
    },
  },
  created() {
    getSummary(this.$route.query.id).then((res) => {
      this.summary = res.data[0];
      getBookChapters(res.data[0]._id).then((res) => (this.chapters = res.data.chapters));
    });
  },
};
</script>

<style lang="less" scoped>
.chapter {
  ul {
    .chapter-header {
      width: 100%;
      padding: 15px 20px;
      font-size: 16px;
      border-bottom: 1px solid #f8f8f8;
      .chapter-headers {
        display: inline;
        .chapter-title {
          font-weight: 900;
          font-size: 14px;
          display: inline;
        }
        .chapter-title-desc {
          margin-left: 10px;
          color: #999;
          font-size: 12px;
        }
      }
      .chapterBtn {
        float: right;
        font-size: 14px;
      }
    }
    .chapter-body {
      font-size: 12px;
      color: #999;
      li {
        height: 48px;
        line-height: 4;
        border-bottom: 1px solid #f8f8f8;
        margin-left: 20px;
        position: relative;
        .vip {
          position: absolute;
          top: 16px;
          right: 15px;
          width: 14px;
          height: 16px;
          background: url(data:image/png;base64,)
            50% no-repeat;
          background-size: 90% 90%;
          border-radius: 2px;
        }
      }
    }
  }
}
</style>
