<template>
  <div class="search">
    <BookTitle>搜索</BookTitle>
    <section class="searchcon">
      <van-search
        v-model="value"
        @focus="inputing = true"
        @blur="inputing = false"
        @search="
          onSearch(value);
          searching = true;
        "
        @keyup.enter="onSearch;"
        shape="round"
        placeholder="请输入搜索内容"
      ></van-search>
    </section>
    <!-- 热门搜索 -->
    <section class="hots" v-if="!value && !searching">
      <div class="hots-title">
        <div class="title">热门推荐</div>
        <div class="right refresh">换一批</div>
      </div>
      <ul>
        <li
          @click="
            searching = true;
            value = t.word;
            onSearch(value);
          "
          v-for="(t, i) in sear"
          :key="i"
        >
          {{ t.word }}
        </li>
      </ul>
    </section>
    <!-- 搜索建议 -->
    <section class="suggests" v-if="value && !searching">
      <h3 class="title">搜索“{{ value }}”</h3>
      <ul>
        <li
          v-for="(item, index) in suggests"
          :key="index"
          @click="
            searching = true;
            value = item;
            onSearch(value);
          "
        >
          <i class="svg-search"></i>
          <span>{{ item }}</span>
        </li>
      </ul>
    </section>
    <!-- 搜索结果 -->
    <section class="searchResults" v-if="searching">
      <ul>
        <BookItem v-for="(item, index) in lists" :key="index" :book="item"></BookItem>
      </ul>
      <p v-if="!hasMore">没有更多了</p>
    </section>
  </div>
</template>

<script>
import BookTitle from "@/components/BookTitle.vue";
import BookItem from "@/components/BookItem.vue";
import { getPopularSearches, getSearch, getSearchComplete } from "@/apis/index.js";
export default {
  components: {
    BookTitle,
    BookItem,
  },
  data() {
    return {
      value: "",
      lists: null,
      sears: null,
      sear: null,
      suggests: [],
      searching: false,
      inputing: false,
      hasMore: false,
    };
  },
  methods: {
    onSearch(val) {
      // console.log(val);
      getSearch(val).then((res) => {
        this.lists = res.data.books;
      });
    },
    onSearchhder() {
      this.onSearch();
    },
  },
  created: function() {
    getPopularSearches().then((res) => {
      this.sears = res[1].data.searchHotWords;
      this.sear = this.sears.slice(0, 8);
    });
  },
  watch: {
    value(n) {
      if (this.inputing) {
        this.searching = false;
      }
      if (n && !this.searching) {
        getSearchComplete(n).then((res) => (this.suggests = res.data.keywords));
      } else {
        this.suggests = [];
      }
    },
  },
};
</script>

<style lang="less" scoped>
.search {
  // .searchcon {
  // }
  .hots {
    padding: 0 5vw;
    width: 100vw;
    .hots-title {
      display: flex;
      justify-content: space-between;
      align-content: center;
      margin-bottom: 10px;
      .title {
        color: #6b6b6b;
        font-size: 20px;
      }
      .right {
        font-size: 12px;
        padding-right: 15px;
        background-image: url(data:image/png;base64,);
        background-size: 15px 15px;
        background-position: 105% 2%;
        background-repeat: no-repeat;
      }
    }
    ul {
      overflow: hidden;
      li {
        float: left;
        padding: 0 10px;
        margin: 0 10px 10px 0;
        background: #efeff4;
        line-height: 2.2;
        font-size: 13px;
        color: #9b9ba3;
        border-radius: 15px;
      }
    }
  }
}
</style>
