<template>
  <div class="ranking">
    <BookTitle>排行榜</BookTitle>
    <van-tabs v-model="activeName" @click="onClick">
      <van-tab title="男生" name="male"></van-tab>
      <van-tab title="出版" name="epub"></van-tab>
      <van-tab title="女生" name="female"></van-tab>
    </van-tabs>
    <section class="con">
      <van-sidebar v-model="activeKey" @change="onChange">
        <van-sidebar-item v-for="t in titlekey" :key="t._id" :title="t.title | txtcl" />
      </van-sidebar>
      <section class="books" v-if="books">
        <BookItem v-for="book in books" :key="book._id" :book="book" />
        <p v-if="!books[0]">
          <van-loading color="#b93321">加载中...</van-loading>
        </p>
      </section>
    </section>
  </div>
</template>

<script>
import { getRankCategory, getRank } from "@/apis/index.js";
import BookTitle from "@/components/BookTitle.vue";
import BookItem from "@/components/BookItem.vue";
export default {
  components: {
    BookTitle,
    BookItem,
  },
  data() {
    return {
      epub: null,
      female: null,
      male: null,
      titlekey: null,
      books: [],
      activeKey: 0,
      activeName: "male",
    };
  },
  filters: {
    txtcl(t) {
      t = t.replace("Top100", "");
      return t;
    },
  },
  methods: {
    onChange(index) {
      getRank(this.titlekey[index]._id).then((res) => (this.books = res.data.ranking.books));
    },
    onClick(name) {
      this.activeKey = 0;
      if (name === "epub") {
        this.titlekey = this.epub;
        getRank(this.titlekey[0]._id).then((res) => (this.books = res.data.ranking.books));
      } else if (name === "female") {
        this.titlekey = this.female;
        getRank(this.titlekey[0]._id).then((res) => (this.books = res.data.ranking.books));
      } else {
        this.titlekey = this.male;
        getRank(this.titlekey[0]._id).then((res) => (this.books = res.data.ranking.books));
      }
    },
  },
  created() {
    getRankCategory().then((res) => {
      this.epub = res.data.epub;
      this.female = res.data.female;
      this.male = res.data.male;
      this.titlekey = this.male;
      getRank(this.titlekey[0]._id).then((res) => (this.books = res.data.ranking.books));
    });
  },
};
</script>

<style lang="less" scoped>
.ranking {
  .con {
    // display: flex;
    .van-sidebar {
      float: left;
      width: 23vw;
      height: ceil(100vh - 13vh);
      overflow: scroll;
      .van-sidebar-item {
        font-size: 12px;
      }
    }
    .books {
      width: 77vw;
      height: ceil(100vh - 13vh);
      overflow: scroll;
      p {
        text-align: center;
      }
    }
  }
}
</style>
