const getSearch = (val) => {
    val = decodeURI(val)
    return window.axios.get("https://novel.kele8.cn/search?keyword=" + val); //搜索
};
const getSearchComplete = (val) => {
    val = decodeURI(val)
    return window.axios.get("https://novel.kele8.cn/auto-complete?query=" + val); //搜索
};
